.App {
  text-align: center;
  font-family: "Rowdies", sans-serif;
  font-weight: 400;
  background-color:rgb(155,216,240) ;
  color: rgb(244,242,226);
}

.title{
  font-size: 100px;
  color:rgb(255,255,242) ;
  border-bottom: 5px solid rgb(244,242,226);
  width: 85%;
  margin: 0 auto;
}

.search-title{
  font-size: 24px;
}

.search-container{
  display: flex;
}

.city-search{
  margin: 15px ;
  width: 200px;
  height: 25px;
  color:rgb(244,242,226);
}

input.city-search,
input.noe{
  border: solid 2px rgb(236,223,173);
  border-radius: 12px;
  background-color: rgb(244,242,226);
}

.noe{
  padding:5px;
}

.search-bar{
  /* margin-top: 250px; */
}

.search-bar,
.visualization{
  flex: 1;
  padding: 10px;
}

.responsive-container{
  margin:20px;
}

li{
  list-style-type: none;
}

button{
  font-size: 12px;
  padding: 12px;
  margin:5px;
  border: none;
  border-radius:7px;
  background-color: rgb(106, 205, 244);
  box-shadow: 0 8px 16px 0 rgb(88, 189, 228), 0 6px 20px 0 rgba(0,0,0,0.19);
}

.alerts-container{
  position: fixed;
  top: 0px;
  left: 20px;
  width: 250px;
  z-index: 10000;
}

